module.exports = "<h1 id=\"markt-bersicht-ai-services\">Marktübersicht AI Services</h1>\n<h2 id=\"clarissa-digitale-beraterin\">Clarissa, digitale Beraterin</h2>\n<p>Bei <a href=\"https://www.penseo.de\">Penseo</a> haben wir uns der betrieblichen Altersversorgung verschrieben. Wir wollen diese Form der Geldanlage wieder zu einem attraktiven Produkt machen, welches dem Unternehmen minimalen Verwaltungsaufwand verursacht und dem Arbeitnehmer eine lukrative Anlage für das Rentenalter bietet. Dabei digitalisieren wir alle anfallenden Geschäftsprozesse und beraten den Arbeitgeber per Chatbot fair und umfänglich zum Thema betriebliche Altersversorgung.</p>\n<p>Durch den Einsatz eines Chatbot im Beratungsprozess können wir auf Abschlussprovisionen verzichten, die den Ertrag der bAV in den ersten Jahren erheblich schmälern können. Die Provisionen werden normalerweise an die beteiligten Makler ausgeschüttet um ihre Akquisitionskosten zu decken. Diese Kosten fallen bei Penseo nicht an, da wir den Makler weitestgehend durch einen digitalen Berater in unserem virtuellen Beratungsgespräch ersetzen. Clarissa, unser Chat-Bot zur betrieblichen Altersversorgung, benötigt hierfür fachliches know-how und die Fähigkeit sich im Live-Chat mit den Arbeitnehmern auszutauschen und sie angemessen zu beraten.</p>\n<h2 id=\"do-you-speak-german-\">Do you speak german?</h2>\n<p>Als Lean-Startup sind wir bei Penseo agil, sprich Schritt für Schritt vorgegangen. Zunächst hat unser Bot eine ansprechende User-Experience und ein modernes Design erhalten. Danach haben wir den Chat-Verlauf als State-Machine implementiert, also die Schritte und Übergänge definiert, die für einen rechtssicheren Beratungsprozess erforderlich sind. Anschließend haben wir eine Texterkennung auf Basis von Pattern-Matching und Wortlisten angebunden. Es wurde jedoch sehr schnell ersichtlich, dass diese Lösung zu limitiert ist und hier die erweiterten Fähigkeiten aus dem Bereich Artificial Intelligence, besonders Machine-Learning &amp; Natural-Language-Processing benötigt werden.</p>\n<p>Da Penseo ein Produkt ist, welches sich weitestgehend an deutschsprachige Anwender richtet und erst nachgelagert englische Sprache anbieten muss, war es für uns besonders wichtig bei der Auswahl einer Lösung darauf zu achten, dass eine Unterstützung der deutschen Sprache entsprechend gegeben ist.</p>\n<h2 id=\"der-markt-und-die-anforderungen\">Der Markt und die Anforderungen</h2>\n<p>Viele der großen Softwarehersteller und Cloud-Provider arbeiten an Artificial-Intelligence &amp; Machine-Learning Produkten. Bei der Auswahl eines Dienstes muss stark darauf geachtet werden, welche Use-Cases unterstützt werden und wie die Sprach-Abdeckung für die gewünschte Zielsprache ist. Oft müssen unterschiedliche Dienste eines Hersteller wie beispielsweise Voice-Recognition &amp; Natural Language Processing kombiniert werden, um das gewünschte Ergebnis zu erreichen.</p>\n<p>Neben Englisch unterstützen viele Dienste inzwischen auch Deutsch und andere Sprachen. Die Qualität der deutschsprachigen Varianten ist in der Regel schlechter als die englischen. Oft stellen die Anbieter neue Features nur für Englisch bereit, auf deutsche Varianten muss man teils lange warten. Als Beispiel sei hier der <a href=\"https://aws.amazon.com/de/documentation/lex/\">AWS-Dienst Lex</a> erwähnt, der nun schon über ein halbes Jahr lediglich als englischsprachige Variante bereitsteht.</p>\n<p>In der Regel bieten die Dienste kostenfreie Varianten oder Trial-Versionen an, durch die man den technischen Fit für die eigenen Anforderungen überprüfen kann. Oft gibt es auch ein kostenfreies Kontingent an Requests, womit sich Prototypen ohne großen Kosten-Overhead herstellen lassen.</p>\n<h2 id=\"gemeinsamkeiten\">Gemeinsamkeiten</h2>\n<p>Schaut man sich die unterschiedlichen Dienste an, dann lassen sich einige Gemeinsamkeiten entdecken. Das Natural Language Processing bietet dabei im wesentlichen Funktionalitäten zur Erkennung von <em>Keywords</em> (z.B. Synonyme), <em>Entities</em> (z.B. Automarken), <em>Categories</em> (z.B. Sport), <em>Emotions</em> (z.B. Liebe), <em>Sentiments</em> (z.B. positiv), <em>Relations</em> (Beziehung von Textteilen) und <em>Concepts</em> (Abstrakte inhaltliche Erkennung).</p>\n<p>Vielfach ist es möglich eigene spezifische, sprachliche Konzepte durch das Anlegen eigener <em>Models</em> abzubilden.</p>\n<p>Schnittstellen von Conversational-AI arbeiten weitestgehend mit <em>Intents</em>, die eine Benutzer-Absicht repräsentieren (z.B. &quot;Ich möchte eine Pizza bestellen&quot;) und <em>Conversations</em> mit denen sich Gesprächsverläufe modellieren lassen.</p>\n<p>Im Deutschen fehlen häufig ganze Funktionalitäten, oder die Trefferwahrscheinlichkeit bei der Spracherkennung ist wesentlich schlechter als im Englischen. Es ist jedoch zu beobachten, dass die Hersteller stark daran arbeiten die Sprachunterstützung zu verbessern.</p>\n<p>Die Dokumentation der Schnittstellen und die zur verfügung gestellten Bibliotheken, Toolkits etc, und die damit verbundene Accessibility, sind überwiegend gut. Das mag daran liegen, dass diese Dienste relativ jung sind und mit modernen Frameworks und Entwicklungsmethoden entwickelt wurden. Man merkt deutlich, dass &quot;Developer Happyness&quot; bei der Konzeptionierung eine immer größere Rolle spielt. Zufriedene Entwickler helfen dabei die Dienste erfolgreich zu vermarkten.</p>\n<h2 id=\"anbieter-in-der-bersicht\">Anbieter in der Übersicht</h2>\n<p>Einen allgemeinen Qualitätsvergleich der Anbieter zu erstellen ist schwierig, da sich die Dienste und Bibliotheken stark in den zugrunde liegenden Konzepten unterscheiden. Die Problematik lässt sich gut im Paper <em>[&quot;Evaluating Natural Language Understanding Services for Conversational Question Answering Systems</em>&quot;](<a href=\"https://www.researchgate.net/publication/319127909_Evaluating_Natural_Language_Understanding_Services_for_Conversational_Question_Answering_Systems\">https://www.researchgate.net/publication/319127909_Evaluating_Natural_Language_Understanding_Services_for_Conversational_Question_Answering_Systems</a>) der TU-München nachlesen. Ein objektiver Vergleich in deutscher Sprache wird durch die schwankende Sprachunterstützung noch schwieriger.</p>\n<p>Die tatsächliche Qualität der Services lässt sich auch erst mittelfristig, nach dem Training der Machine-Learning-Algorithmen, feststellen. Hier arbeiten die Anbieter mit unterschiedlichen Methoden. Oft ist es möglich interaktiv zu überprüfen wie Texte interpretiert werden.</p>\n<p>Hier muss man sich darauf einstellen selbst viele, qualitativ hochwertige, breit gefächerte Trainingsdaten zur Verfügung zu stellen und kontinuierlich Qualitätskontrolle zu betrieben um auch langfristig gute Ergebnisse erreichen zu können.</p>\n<h3 id=\"-ibm-bluemix-watson-https-www-ibm-com-cloud-computing-bluemix-de-watson-\"><a href=\"https://www.ibm.com/cloud-computing/bluemix/de/watson\">IBM Bluemix - Watson</a></h3>\n<p>IBM bietet ein umfangreiches Portfolio an KI Diensten von Spracheingabe und Sprachausgabe, über Natural Language Processing (NLP) und Sentiment Analysis bis hin zu Conversational-AI-Schnittstellen.</p>\n<p>Deutsche Sprachunterstützung ist in vielen Bereichen allgemein verfügbar und alle Sprachvarianten erfahren fortlaufende Verbesserungen. Neue Features werden auch hier englischsprachig entwickelt. Modelle können mit eigenen Daten trainiert werden, dadurch können limitationen der Sprachunterstützung umgangen werden.</p>\n<p>In der umfangreichen Dokumentation von Watson finden sich auch diverse <a href=\"https://console.bluemix.net/docs/services/conversation/sample-applications.html#sample-apps\">Tutorials und Beispiel-Applikationen</a>. Nicht nur die Beispiele, auch die Bibliotheken und auch große Teile der Dokumentation sind bei IBM als <a href=\"https://github.com/watson-developer-cloud/\">Open Source auf GitHub bereitgestellt</a> und können dort weiterentwickelt werden.</p>\n<h3 id=\"-google-cloud-ai-https-cloud-google-com-products-machine-learning-\"><a href=\"https://cloud.google.com/products/machine-learning/\">Google Cloud AI</a></h3>\n<p>Google bietet in der Kategorie &quot;Google Cloud Machine Learning&quot; ein vergleichbares Angebot an Diensten wie IBM Watson. Von Video-, Bild- und Textanalyse über Übersetzungsdienste bis hin zu NLP und allgemeinen Machine-Learning (ML) Diensten kann man aus einem reichhaltigen Angebot schöpfen.</p>\n<p>Die ML-Dienste sind bereits die 2. Generation von Angeboten. Dienste wie der <a href=\"https://cloud.google.com/prediction/docs/\">&quot;Google Prediction Service&quot;</a> werden <a href=\"https://cloud.google.com/prediction/docs/end-of-life-faq\">nach und nach eingestellt</a>.</p>\n<p>Dass Google ein großes Interesse an der Weiterentwicklung dieser Dienste hat kann man auch <a href=\"https://techcrunch.com/2016/09/19/google-acquires-api-ai-a-company-helping-developers-build-bots-that-arent-awful-to-talk-to/\">an der Übernahme</a> von <a href=\"https://api.ai/\">API.ai</a> sehen (<a href=\"#heading=h.jqu0u6hsbtvl\">mehr Infos zu API.ai weiter unten</a>).</p>\n<h3 id=\"-microsoft-cognitive-services-https-docs-microsoft-com-de-de-azure-cognitive-services-\"><a href=\"https://docs.microsoft.com/de-de/azure/cognitive-services/\">Microsoft Cognitive Services</a></h3>\n<p>Microsoft bietet mit &quot;Cognitive Services &amp; Language Understanding&quot; ein ähnliches Portfolio. Auch hier sind Qualität und Umfang vergleichbar mit Google und IBM, wobei die Microsoft Interfaces oft ein wenig umständlich wirken, da bleibt Microsoft seiner Linie treu :)</p>\n<p>Mit <a href=\"https://www.luis.ai/\">&quot;Language Understanding Intelligence Service&quot; (LUIS)</a> bietet Microsoft einen Dienst für Conversational AI an, der den Ansätzen von Watson-Conversation, api.ai (<a href=\"#heading=h.joc7pakqp1xt\">siehe unten</a>) und Amazon Lex (<a href=\"#heading=h.lp7of1hqy9mm\">siehe unten</a>) ähnelt. Mit diesem Service können in einem grafischen User-Interface (GUI) Konversationen für die Interaktion mit Usern über Chatbots oder Sprachsteuerung erstellt werden.</p>\n<p>Für die Erstellung von Chatbots wird das <a href=\"https://docs.microsoft.com/en-us/bot-framework/\">&quot;Microsoft Bot Framework&quot;</a> bereitgestellt. Das Framework stellt ähnlich wie bei api.ai und Watson Schnittstellen und Bibliotheken bereit um Bots auf unterschiedlichen Kanälen wie Slack oder Facebook-Messenger zu integrieren.</p>\n<h3 id=\"-amazon-aws-ai-https-aws-amazon-com-de-amazon-ai-\"><a href=\"https://aws.amazon.com/de/amazon-ai/\">Amazon AWS AI</a></h3>\n<p>AWS bietet mit <a href=\"https://aws.amazon.com/de/lex/\">Lex</a> und <a href=\"https://aws.amazon.com/de/polly/\">Polly</a> Dienste an, mit denen sich Werkzeuge ähnlich wie <a href=\"https://developer.amazon.com/de/alexa-skills-kit\">Amazon-Alexa-Skills</a> erstellen lassen. Zusätzlich bietet <a href=\"https://aws.amazon.com/de/rekognition/\">Rekognition</a> Schnittstellen zur Bilderkennung an.</p>\n<p>Neben der eher dürftigen Produktauswahl und Sprach-Abdeckung der AWS-Dienste, hat AWS noch eine vielzahl von <a href=\"https://aws.amazon.com/de/machine-learning/\">Machine-Learning Amazon-Machine-Images</a> im Angebot, mit deren Hilfe sich beliebte Machine-Learning-Bibliotheken sehr einfach in der Cloud installieren lassen. Dabei profitieren diese Maschinen dann von der weitreichenden AWS Infrastruktur, können individuell konfiguriert, skaliert und erweitert werden. Der Nachteil ist hier die geringe Consumer-Freundlich gegenüber einer SAAS-Lösung.</p>\n<h3 id=\"-dialogflow-aka-api-ai-https-api-ai-\"><a href=\"https://api.ai/\">DialogFlow aka api.ai</a></h3>\n<p>Mit api.ai (gerade zu DialogFlow umbenannt) hat sich Google ähnlich wie bei <a href=\"https://techcrunch.com/2014/10/21/google-acquires-firebase-to-help-developers-build-better-realtime-apps/\">firebase</a> eine Entwickler-Orientierte Plattform eingekauft. Mit api.ai lässt sich sehr einfach eine Conversational-AI über so genannte Agents erstellen, die über eine vielzahl von SDKs und Integrationen beispielsweise in Slack und Facebook-Messenger genutzt werden können. Der Dienst ist auch in deutscher Sprache erhältlich, es sind jedoch wie so oft nicht alle Funktionen verfügbar, zum derzeitigen Stand werden zum Beispiel keine Fallbacks unterstützt.</p>\n<p>Das Training des Machine-Learning-Algorithmus kann sehr einfach über eine Webseite gesteuert werden.</p>\n<p>Durch die Vernetzung der Agents über Webhooks können Prozessketten erstellt werden, die es Entwicklern ermöglichen eine Integration zu bestehenden Systemen herzustellen.</p>\n<h3 id=\"-rasa-ai-http-rasa-ai-\"><a href=\"http://rasa.ai/\">rasa.ai</a></h3>\n<p>Ähnlich wie wit.ai wird auch bei dem Startup RASA mit Sitz in Berlin auf Conversational AI gesetzt und FOSS großgeschrieben. Große Teile der Softwarekomponenten sind <a href=\"https://github.com/RasaHQ/\">auf GitHub verfügbar</a> und können auf eigener Infrastruktur betrieben werden. Für große Unternehmen werden auch Enterprise-Pläne angeboten, die auch beispielsweise das Hosting beinhalten können.</p>\n<p>Die Dienste von RASA basieren weitestgehend auf der Kombination von bestehenden Python-Frameworks und nutzt für das Natural-Language-Processing unter anderem die Bibliothek spaCy von dem ebenfalls in Berlin sitzenden Startup explosion.ai. Für die gute unterstützung in deutscher Sprache ist in RASA Natural-Language-Understanding (NLU) ein <a href=\"https://explosion.ai/blog/german-model\">weitreichendes, deutsches Sprach-Modell</a> eingebunden.</p>\n<p>Neben NLU <a href=\"https://medium.com/rasa-blog/a-new-approach-to-conversational-software-2e64a5d05f2a  https://explosion.ai/blog/german-model\">bietet RASA-Core nun die Möglichkeit</a>, Konversationen durch den Einsatz von ML-Algorithmen dynamisch zu steuern, an Nutzer anzupassen und stetig zu lernen.</p>\n<h3 id=\"-wit-ai-https-wit-ai-\"><a href=\"https://wit.ai/\">wit.ai</a></h3>\n<p>Bei wit.ai wird nach dem Free-Open-Source-Software (FOSS) Ansatz gearbeitet. Alle Komponenten sind kostenlos nutzbar und große Teile des Codes sind <a href=\"https://github.com/wit-ai\">auf GitHub verfügbar</a>. Das Angebot von wit.ai ähnelt dem von api.ai, es gibt jedoch neben NLP nur sehr wenig Funktionalität. Dies erklärt wahrscheinlich auch den recht niedrigen Aktivitätsgrad der Github-Projekte. api.ai bietet hier das deutlich attraktivere Produkt.</p>\n<h3 id=\"-recast-ai-https-recast-ai-\"><a href=\"https://recast.ai/\">recast.ai</a></h3>\n<p>Das Angebot von recast.ai ähnelt dem von wit.ai und richtet sich an Entwickler von Conversational-Bots. Deutsch wird neben anderen Sprachen zwar unterstützt, der Fokus liegt aber auch hier auf der Entwicklung von englischsprachigen Bots.</p>\n<h3 id=\"-reply-ai-https-www-reply-ai-\"><a href=\"https://www.reply.ai/\">reply.ai</a></h3>\n<p>Bei dem grafisch sehr ansprechenden Startup reply.ai gibt es diverse Bots für den Use-Case Customer-Support. Es soll hier Conversational AI, Bot Frameworks und Analytics aus einer Hand geben. Integrationen in Dienste wie Facebook-Messenger, Slack und weiteren Chat-Diensten stehen zur Verfügung. reply.ai ermöglicht es einem sofort über die Website einen Beispiel-Bot mit <a href=\"https://www.zendesk.com/\">ZenDesk-Integration</a> per Chat im Facebook-Messenger zu erstellen. Leider sind mehr Details nicht zu erfahren, da es hier keine offenen Trial-Zugänge gibt.</p>\n<p>Reply.ai ist Autor des <a href=\"http://www.chatbotsweekly.com/\">Chatbot Weekly</a><a href=\"http://www.chatbotsweekly.com/\"> Newsletters</a>, der stetig Informationen rund um das Thema Chatbots sammelt.</p>\n<h3 id=\"-meya-ai-https-meya-ai-\"><a href=\"https://meya.ai/\">meya.ai</a></h3>\n<p>meya.ai ist ein weiteres Startup mit klarem Entwickler-Fokus. Es bietet eine Plattform, die den kompletten Entwicklungszyklus eines Bots im Browser darstellen soll. Dabei kann ein Bot in node.js oder Python entwickelt werden. Da derzeit in der Dokumentation <a href=\"https://docs.meya.ai/docs/bot-cms-languages\">keine Informationen über die Unterstützung</a> der deutschen Sprache zur Verfügung stehen, wurde der Dienst nicht weiter unter die Lupe genommen.</p>\n<h2 id=\"favoritenliste\">Favoritenliste</h2>\n<p>Das Angebot von Artificial-Intelligence, Machine-Learning und Natural-Language-Processing ist bereits sehr vielfältig. Es entstehen laufend neue Startups, die sich diesem Hype-Thema anschließen und die Benutzerfreundlichkeit steigern, Einstiegshürden reduzieren und AI zu einem Consumer-freundlichen, nutzbaren Werkzeug werden lassen.</p>\n<p>Es ist zu erwarten, dass sich weitere Player, wie möglicherweise die Telekom, die derzeit nur in ihrem Blog <a href=\"https://www.welove.ai\">welove.ai</a> berichtet, in diesem Markt betätigen und hier auch zu einer besseren Unterstützung von deutschsprachigen Lösungen beitragen.</p>\n<p>Aus Sicht von Penseo bietet IBM mit Watson derzeit eines der leistungsstärksten Angebote im Bereich AI. Die weitreichende Dokumentation der Schnittstellen, die zahlreichen Beispiel-Applikationen und die starke Unterstützung im deutschsprachigen Bereich machen sie zu einem der Favoriten.</p>\n<p>Da wir bei Penseo mit vielen sensiblen Kundeninformationen umgehen müssen, bevorzugen wir jedoch eine Lösung, bei der wir die Hoheit über die Daten behalten. Die Komponenten von RASA können uns hier eine gute Sprachunterstützung und vielversprechende Machine-Learning Ansätze bieten, die unsere digitalen Berater fast wie menschliche Gesprächspartner erscheinen lassen. Darüber hinaus sind wir selber große Befürworter von Open-Source-Software und versuchen möglichst viele unserer Komponenten als FOSS auf <a href=\"https://github.com/penseo/\">GitHub zur Verfügung zu stellen</a>.</p>\n";

